<template>
  <main>
    <section class="search container">
      <div class="row">
        <div class="col-10 offset-1">
          <form class="form">
            <div class="input-group input-lg">
              <div class="input">
                <input
                type="text"
                name="keyowrd"
                id="keyowrd"
                class="form-control"
                v-model="keyowrd"
                placeholder="輸入客戶名稱查詢">
              </div>
              <button
              class="btn btn-primary btn-icon"
              type="submit"
              @click.stop.prevent="doSearch()">
                <i class="icon-search i-24"></i>
              </button>
            </div>
          </form>
        </div>
      </div>
    </section>

    <section class="container-fluid">
      <h4 class="text-green">
        群組列表：{{this.$route.query.grp_name}}
      </h4>
      <div :class="{
        'section-loading': isLoading_1
      }">
        <table id="datatable" class="table datatable table-striped table-bordered">
          <thead>
            <tr>
              <th width="140px">
                <div class="checkbox checkbox-inline mr-0">
                  <input type="checkbox" name="check-all" id="check-all" value="1" v-model="checkAll">
                  <label for="check-all"></label>
                </div>
                #
              </th>
              <th width="100px">姓名</th>
              <th>身分證</th>
              <th>手機</th>
              <th>電話</th>
              <th>生日</th>
            </tr>
          </thead>
          <tbody v-if="tableData.length">
            <tr v-for="(d, did) in tableData" :key="`td-${did}`">
              <td>
                <div class="checkbox checkbox-inline mr-0">
                  <input type="checkbox" name="td-item" :id="`check-${did}`" :value="d.identityID" v-model="selection">
                  <label :for="`check-${did}`"></label>
                </div>
                <span class="no">{{did + 1}}</span>
                <span class="info text-center">
                  <level :lv="d.lv" />
                  <avatar :gender="d.gender" :size="20" />
                </span>
              </td>
              <td>
                <router-link
                :to="{path: `/detail/${d.id}/tab/1`, query: {csid: d.identityID}}"
                class="link text-underline">
                  {{d.name}}
                </router-link>
              </td>
              <td>{{d.identityID}}</td>
              <td>{{d.mobile}}</td>
              <td>{{d.phone}}</td>
              <td>
                <div class="date">{{d.birthday | dateFormat}}</div>
                <div class="age">
                  <b>{{d.ages}}</b>歲
                </div>
              </td>
            </tr>
          </tbody>
          <tbody v-else>
            <tr>
              <td colspan="7" class="empty">
                <h3>此群組無客戶資料</h3>
              </td>
              <td v-show="false"></td>
              <td v-show="false"></td>
              <td v-show="false"></td>
              <td v-show="false"></td>
              <td v-show="false"></td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="row mx-0">
        <div class="col-12 text-right">
          <button class="btn btn-primary"
          @click="panelOpen('printModalOpen')"
          :disabled="!selection.length">
            列印名條
          </button>
        </div>
      </div>
    </section>

    <print-modal
    :open="printModalOpen"
    :custs="selection"
    @close="panelClose('printModalOpen')"/>
  </main>
</template>

<script>
import {commonMixin} from '../../mixins/common'
import {mapActions, mapGetters} from 'vuex'
import PrintModal from '../components/PrintModal'
import Avatar from '../../components/Avatar'
import Level from '../../components/Level'

import { getGroupMember } from '../../service'

export default {
  name: 'GroupMember',
  mixins: [commonMixin],
  watch: {
    'checkAll': 'toggleCheckAll'
  },
  computed: {
    ...mapGetters(['BaseUrl'])
  },
  components: {
    PrintModal, Avatar, Level
  },
  data () {
    return {
      isLoading_1: false,
      printModalOpen: false,
      checkAll: false,
      selection: [],
      keyowrd: '',
      tableData: [],
      memDatas: []
    }
  },
  methods: {
    ...mapActions([
      'setPageLock'
    ]),
    toggleCheckAll (val) {
      if (val) {
        this.selection = this.tableData.map(item => {return item.identityID})
      } else {
        this.selection = []
      }
    },
    initGroupMember() {
      this.selection = []
      this.isLoading_1 = true
      getGroupMember(this.$store.state.userInfo.id, this.$route.query.grp_id).then(result => {
        if(result) {
          this.memDatas = result.map((mem, index) => {
            return {
              id: index,
              lv: Number(mem.vip_lvl) || 0,
              gender: Number(mem.sex) || 0,
              name: mem.name,
              identityID: mem.id,
              mobile: mem.mobile || '',
              phone: (mem.home_tel_area && mem.home_tel)? `${mem.home_tel_area}-${mem.home_tel}`:(mem.home_tel? mem.home_tel: mem.home_tel_area),
              birthday: mem.birthday,
              ages: mem.age
            }            
          })
        } else {
          this.memDatas = []
        }
        this.tableData = this.memDatas.map(item => item)
        console.log(this.tableData);
        
        this.isLoading_1 = false
      })
    },
    doSearch () {
      this.tableData = this.memDatas.filter(item => item.name.indexOf(this.keyowrd)> -1)
    }
  },
  mounted () {
    this.initGroupMember()

    window.$("#datatable").dataTable({
      scrollY: '380px',
      paging: false,
      searching: false,
      ordering: true,
      columnDefs: [
        { orderable: false, targets: [0, 4, 5] }
      ]
    });
  }
}
</script>
